<template>
  <div id="app">
    <topbar></topbar>
    <router-view></router-view>   
    <player></player>
  </div>
</template>
<script>
import topbar from './components/topbar.vue';
import player from './components/player.vue';
export default {
  components:{
    topbar,
    player
  },
  methods:{
    setSequenceList(){
      let str=localStorage.getItem("sequenceList");
      let arr =JSON.parse(str) || []
      this.$store.commit('setSequenceList',arr);
    },
    setLikeList(){
      let str=localStorage.getItem("likeList");
      let arr =JSON.parse(str) || []
      this.$store.commit('setLikeList',arr);
    }
  },
  created(){
    this.setLikeList();
    this.setSequenceList();
  }
}
</script>
