<template>
  <div id="songlist">
      <div class="herder">
          <img :src="herder.cover" alt="">
      </div>
      <div class="box">
             <p>{{ herder.title }}</p>
             <p v-html="herder.info"></p>
        </div>
      <div class="listbox">
          <ul>
              <li v-for="item in songlist" :key="item.id" @click="addmuisc(item)">
                  <p class="name">{{ item.name }}</p>
                  <p class="info">{{ item.singer[0].name }}.{{ item.album.name }}</p>
              </li>
          </ul>
      </div>
  </div>
</template>

<script>
export default {
    data(){
        return{
            herder:{
                cover:'',
                title:'',
                info:''
            },
            songlist:[]
        }
    },    
    methods:{
        addmuisc(music){
            let song = {
                id:music.id,
                songmid:music.mid,
                // 歌曲名称
                songName:music.name,
                //歌手
                singer:music.singer,
                // 封面地址
                coverUrl:this.herder.cover,
                // 专辑
                albummid:music.album.mid,
                // 专辑名称
                albumname:music.name,
            }
            this.$store.commit("addSongToSequenceList",song);
            this.$store.commit("setSong",song)
            //改变索引指向最后一个
            // this.$store.commit('footIndexes'); 
        },
        getList(){ 
            let url='http://111.229.195.198:9527/api/getDiscList';
            this.$jsonp(url,{
                type: 1,
                json: 1,
                utf8: 1,
                onlysong: 0,
                new_format: 1,
                disstid: this.$route.query.id,
                g_tk: 5381,
                loginUin: 0,
                hostUin: 0,
                format: 'json',
                inCharset: 'utf8',
                outCharset: 'utf-8',
                notice: 0,
                platform: 'yqq.json',
                needNewCode:0
            }).then(res =>{
                this.herder.cover = res.cdlist[0].dir_pic_url2 || res.cdlist[0].logo;
                this.herder.title = res.cdlist[0].dissname;
                this.herder.info = res.cdlist[0].desc;
                // 干掉收费的
                for(let i=res.cdlist[0].songlist.length-1;i>=0;i--){
                    if(res.cdlist[0].songlist[i].pay.pay_play==1){
                        res.cdlist[0].songlist.splice(i, 1);
                    }
                }
                this.songlist =  res.cdlist[0].songlist; 
            })
           
        }
  },
  mounted(){
      this.getList()
  }

}
</script>

<style lang='less' scoped>
    @bf:25;
    #songlist{
        .herder{
            position: relative;
            padding-top:10px;
            height:300px;
            background:#666;
            img{
                position:absolute;
                top:0;
                left:0;
                right:0;
                bottom:0;
                margin: auto;
                height:300px;
                width:100%;
                max-width:300px;
                object-fit: cover;
                border-radius:10px;
            }
        }
        .box{
            position:relative;
            top:-20px; 
            width:90%; 
            left:5%;
            background-color:#fff;
            border:1px solid #ccc;
            padding:10px;
            font-size:14rem / @bf;
            color:#333;
            box-sizing: border-box;
            border-radius:10px;         
        }
        .listbox{
                li{
                    padding:10rem / @bf;
                    padding-left:15rem / @bf;
                    border-bottom:1px solid #ddd; 

                    .name{
                        font-size:14rem / @bf;
                    }
                    
                    .info{
                        margin-top:4rem / @bf;
                        font-size:12rem / @bf;
                    }
                }
                p{
                    color:#222;
                }
            }
    }
</style>
