<template>
    <div class="search">
        <div class="search-from">
            <input type="text" v-model="testmsg" @change="search" name="" id="">
        </div>
        <hot-search @func="searchkey" v-show="!ishothide"></hot-search>
        <div class="musiclist">
            <ul>
                <li v-for="(music,index) in musiclist" @click="addmusic(music)" v-bind:key='index'>
                    <span class="music-name">{{ music.songname }}</span>
                    <span class="info">{{ music.singer[0].name }} - {{ music.albumname}} </span>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import hotSearch from './hotSearch'
export default {
    components:{
        hotSearch
    },
    data() {
        return {
            testmsg:'',
            musiclist:[],
            page:1,
            ishothide:false
        }
    },
    methods:{
        inspect(){
            let str = this.testmsg.trim()
            if(str.length > 0){
                this.ishothide = true
            }else{
                this.ishothide = false
                this.musiclist=[]
                this.page=1
            }
        },
        // 搜索
        search(){
            let str = this.testmsg.trim();
            if(str.length > 0){
                console.log('this search');
                let url='https://c.y.qq.com/soso/fcgi-bin/client_search_cp?aggr=1&cr=1&flag_qc=0&n=30';
                this.$jsonp(url,{w: this.testmsg,p:this.page, callbackName: "callback"})
                .then(json =>{
                    for(let i=json.data.song.list.length-1;i>=0;i--){
                        if(json.data.song.list[i].pay.payplay==1){
                            json.data.song.list.splice(i, 1);
                        }
                    }
                    this.musiclist=this.musiclist.concat(json.data.song.list);
                })
                .catch(err => {
                    console.log('搜索失败' + err);
                })
            }
            
        },
        // 接受子组件传来的值
        searchkey(j){
            this.testmsg = j;
            this.musiclist = [];
            this.search();
        },
        
        // 播放
        addmusic(music){
            let song = {
                id:music.songid,
                songmid:music.songmid,
                // 歌曲名称
                songName:music.songname,
                //歌手
                singer:music.singer,
                // 封面地址
                coverUrl:'http://imgcache.qq.com/music/photo/album_300/'+(music.albumid)%100+"/300_albumpic_"+music.albumid+"_0.jpg",
                // 专辑
                albummid:music.albummid,
                // 专辑名称
                albumname:music.albumname,
            }
            this.$store.commit("addSongToSequenceList",song);
            this.$store.commit("setSong",song)
        },
        // 下拉加载
        scroll(){
            window.onscroll = () => {
	            var scrollHeight = Math.max(document.documentElement.scrollHeight, document.body.scrollHeight);
	            //滚动条滚动距离
	            var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
	            //窗口可视范围高度
	            var clientHeight = window.innerHeight || Math.min(document.documentElement.clientHeight, document.body.clientHeight);
	            if (clientHeight + scrollTop >= scrollHeight) {
                    this.page ++;
                    this.search();
                }
            } 
        }
    },
    mounted() {
        this.scroll()
    },
    watch:{
        'testmsg'(){
            this.inspect();
        }
    }
}

</script>
<style lang="less" scoped>
@bf:25;
    .search{
        .search-from{
            box-sizing: border-box;
            position: fixed;
            top:50rem / @bf;
            padding:8rem / @bf 10rem / @bf;
            width:100%;
            max-width:1000px;
            background-color:rgb(243, 200, 106);
            
            input[type='text']{
                box-sizing: border-box;
                padding-left:15rem / @bf;
                margin:auto;
                height:30rem / @bf;
                width:100%;
                border-radius:5rem / @bf;
                font-size:16rem / @bf;
            }
        }
        .musiclist{
            margin-top:60rem / @bf;
            ul{
                background-color:#fff;
                margin-bottom:60rem / @bf;
                padding:0 10rem / @bf;

                li{
                    height:50rem / @bf;
                    box-sizing: border-box;
                    padding:7rem / @bf 0;
                    border-bottom:1rem / @bf solid #ddd;

                    .music-name{
                        display:block;
                        height:20rem / @bf;
                        width:100rem / @bf;
                        overflow: hidden;
                        line-height:20rem / @bf;
                        font-size:16rem / @bf;
                    }
                    .info{
                        display:block;
                        height:16rem / @bf;
                        width:100%;
                        overflow: hidden;
                        line-height:16rem / @bf;
                        font-size:14rem / @bf;
                        color:#666
                    }
                }
            }
        }
    }
</style>
