<template>
  <div id="recommend">
    <div class="clause1">
          <h1 class="red">官方歌单</h1>
          <ul>
            <li v-for="item in OfficialSongList" :key="item.id" @click="goSonglist(item.id)">
                <img :src="item.cover" alt="">
                <p>{{ item.title }}</p>
            </li>
          </ul>
      </div>
      <div class="clause1">
          <h1 class="orange">达人歌单</h1>
          <ul>
            <li v-for="item in TalentSongList" :key="item.id" @click="goSonglist(item.id)">
                <img :src="item.cover" alt="">
                <p>{{ item.title }}</p>
            </li>
          </ul>
      </div>
      <div class="clause1">
          <h1 class="orange">其他歌单</h1>
          <ul>
            <li v-for="item in MoreSonglist" :key="item.id" @click="goSonglist(item.dissid)">
                <img :src="item.imgurl" alt="">
                <p>{{ item.dissname }}</p>
            </li>
          </ul>
      </div>  
  </div>
</template>

<script>
export default {
    data(){
        return {
            OfficialSongList:[],
            TalentSongList:[],
            MoreSonglist:[]
        }
    },
    methods:{
        getSongList(){
            let url='https://u.y.qq.com/cgi-bin/musicu.fcg';
            this.$jsonp(
                url,
                {
                    cgiKey:'GetHomePage',
                    data:'{"comm":{"g_tk":5380,"uin":"","format":"json","inCharset":"utf-8","outCharset":"utf-8","notice":0,"platform":"h5","needNewCode":1},"MusicHallHomePage":{"module":"music.musicHall.MusicHallPlatform","method":"MobileWebHome","param":{"ShelfId":[101,102,161]}},"hotkey":{"module":"tencent_musicsoso_hotkey.HotkeyService","method":"GetHotkeyForQQMusicMobile","param":{"remoteplace":"txt.miniapp.wxada7aab80ba27074","searchid":"1559616839293"}}}'
                }
            ).then(json =>{
                this.OfficialSongList =  json.MusicHallHomePage.data.v_shelf[0].v_niche[0].v_card;
                this.TalentSongList =   json.MusicHallHomePage.data.v_shelf[1].v_niche[0].v_card;  
            })
        },
        getMoreSongList(){
            let url= 'http://111.229.195.198:9527/api/getMoreSongList';
            let data = {
                picmid: 1,
                rnd: '0.4501629007875565',
                g_tk_new_20200303: 5381,
                g_tk: 5381,
                loginUin: 0,
                hostUin: 0,
                format: 'json',
                inCharset: 'utf8',
                outCharset: 'utf-8',
                notice: 0,
                platform: 'yqq.json',
                needNewCode: 0,
                categoryId: '10000000',
                sortId: 5,
                sin: 0,
                ein: 19,
            }
            this.$jsonp(url,data).then(res =>{
               this.MoreSonglist = res.data.list;
            })
        },
        goSonglist(id){
             this.$router.push({
                path:'/songlist',
                query:{
                    id:id
                }
            })
        },

    },
    mounted(){
        this.getSongList(),
        this.getMoreSongList()
    }
}
</script>

<style lang="less" scoped>
    @bf:25;
    #recommend{
        margin-top:10px;
        .clause1{
            h1{
                margin:10rem / @bf 0;
                padding:5rem / @bf 0;
                color:#fff;
                text-align: center;
                background-color:#f90;
                font-size:16rem / @bf;
            }
            ul{
                box-sizing:border-box;
                padding:0 10rem / @bf;
                display: flex;
                flex-wrap: wrap;
                justify-content:space-between;
                li{
                   width:30%;
                   margin-bottom:20rem / @bf; 
                   img{
                       border-radius: 20rem / @bf;
                   }
                   p{
                       margin:5rem / @bf;
                        color:#666;
                        font-size:14rem / @bf;
                   }
                }
            }
        }
    }
</style>