<template>
    <div id="hotSearch">
        <p class="title">热门搜索</p>
        <ul class="list">
             <li v-for="item in hotlist" :key="item.n" @click="search(item.k)">
                 {{ item.k }}
             </li>
        </ul>
    </div>
</template>
<script>
export default {
    data(){
        return {
            hotlist:[]
        }
    },
    methods:{
        gethotlist(){
            let url = 'http://111.229.195.198:9527/api/gethotkey';
            this.$jsonp(url).then(res =>{
                this.hotlist = res.data.hotkey;
            }).catch(e =>{
                console.log(e);
            })
        },
        search(k){
            this.$emit('func',k)
        }
        
    },
    mounted(){
        this.gethotlist()
    }
}
</script>
<style lang="less" scoped>
    @bf:25;
    #hotSearch{
        width:100%;
        margin-top:60rem / @bf;
        padding:10px;
        box-sizing: border-box;
       
        .title{
            height:24rem / @bf;
            line-height:24rem / @bf;
            font-size:16rem / @bf;
            border-bottom:2px solid #666;
            color:#666
        }
        .list{
            margin-top:10px;
            font-size:12rem / @bf;
            li{
                display: inline-block;
                margin:10px 0 0 20px;
                height:20rem / @bf;
                line-height:20rem / @bf;
                border:1px solid #333;
                padding:0 10px;
                color:#333;
                border-radius:5px;
            }
        }
    }
</style>
