<template>
  <div id="like-list">
     <div class="title">我喜欢</div>
      <div class="list">
            <ul>
               <li class="item" v-for="song in this.$store.getters.getLikeList" v-bind:key="song.id" @click="play(song)">
                <p class="name">{{ song.songName }}</p>
                <p class="info">{{ song.singer[0].name }} . {{ song.albumname }}</p>
                </li>
            </ul>
      </div>
  </div>
</template>

<script>
export default {
    methods:{
        play(song){
            this.$store.commit("setSong",song)
        }
    }     
}
</script>

<style style lang="less" scoped>
@bf:20;
#like-list{
    .title{
        margin-top:10px;
        height:30rem / @bf;
        width:100%;
        font-size:16rem / @bf;
        font-weight:700;
        text-align: center;
       
    }
    .list{
         li{
            height:60rem / @bf;
            border-top:1px solid #ccc;
            box-sizing:border-box;
            padding:10px;
        }
        .name{
            height:20rem / @bf;
            font-size:14rem / @bf;
        }
        .info{
            height:20rem / @bf;
            line-height:20rem / @bf;
            font-size:12rem / @bf;
            color:#666
        }
    }
}
</style>
