<template>
  <div id="singer-details">
      <div class="herder">
          <img :src="ObjSinger.singer_pic" alt="">
          <span>{{ ObjSinger.singer_name }}</span> 
      </div>
      <div class="list">
            <ul>
              <li v-for="item in songList" :key="item.songInfo.id" @click="addmuisc(item)">
                  <p class="name">{{ item.songInfo.name }}</p>
                  <p class="info">{{ item.songInfo.singer[0].name}}</p>
              </li>
            </ul>
      </div>
  </div>
</template>

<script>
export default {
    data(){
        return {
            ObjSinger:{},
            songList:[],
            stdata:{
                comm:{ct:24,cv:0},
                singerSongList:{
                    method:"GetSingerSongList",
                    param:{order:1,singerMid:"0025NhlN2yWrP4",begin:0,num:300},
                    module:"musichall.song_list_server"
                }
            }
        }
    },
    methods:{
        addmuisc(music){
            let song = {
                id:music.songInfo.id,
                songmid:music.songInfo.mid,
                // 歌曲名称
                songName:music.songInfo.name,
                //歌手
                singer:music.songInfo.singer,
                // 封面地址
                coverUrl:this.ObjSinger.singer_pic,
                // 专辑
                albummid:music.songInfo.album.mid,
                // 专辑名称
                albumname:music.songInfo.name,
            }
            this.$store.commit("addSongToSequenceList",song);
            this.$store.commit("setSong",song)
        },
        getObjSinger(){
            this.ObjSinger = this.$route.params.ObjSinger;
            // 如果对象为空则返回到歌手列表页面
            if( !(this.ObjSinger)){
                this.$router.push({
                    path:'/singer'
                })
            }else{
                if(this.ObjSinger.singer_pic !=''){
                    this.ObjSinger.singer_pic = this.ObjSinger.singer_pic.replace(/150x150/,'300x300');
                }
                this.stdata.singerSongList.param.singerMid  = this.ObjSinger.singer_mid;
            }
           
        },
        getSongList(){
            // 歌曲列表
            let url='https://u.y.qq.com/cgi-bin/musicu.fcg'
            this.$jsonp(url,{
                '-': 'getSingerSong07020464607415722',
                g_tk: 5381,
                loginUin: 0,
                hostUin: 0,
                format: 'json',
                inCharset: 'utf8',
                outCharset: 'utf-8',
                notice: 0,
                platform: 'yqq.json',
                needNewCode: 0,
                data: JSON.stringify(this.stdata)
            }).then(json => {
            //    干掉收费的
               for(let i=json.singerSongList.data.songList.length-1;i>=0;i--){
                    if(json.singerSongList.data.songList[i].songInfo.pay.pay_play==1){
                        json.singerSongList.data.songList.splice(i, 1);
                    }
                }
                this.songList = json.singerSongList.data.songList;
                }).catch(err => {
                    console.log('获取失败');
            })
        }
    },
    mounted(){
        this.getObjSinger(),
        this.getSongList()
    }  
}
</script>

<style lang="less" scoped>
@bf:25;
#singer-details{
    .herder{
        position:relative;
        height:300px;
        width:100%;
        background-color:#777;
        img{
            position:absolute;
            top:0;
            left:0;
            right:0;
            bottom:0;
            margin: auto;
            height:100%;
            max-height:300px;
            width:100%;
            max-width:300px;
            object-fit: cover;
            border-radius:20px;
        }
        span{
            display: block;
            position:absolute;
            width:100%;
            bottom:30px;
            font-size:30px;
            color:#fff;
            text-align: center;
        }
    }
    .list{
        li{
            height:60rem / @bf;
            border-top:1px solid #ccc;
            box-sizing:border-box;
            padding:10rem / @bf;   
        }
        .name{
            height:20rem / @bf;
            font-size:16rem / @bf;
        }
        .info{
            height:20rem / @bf;
            line-height:20rem / @bf;
            font-size:12rem / @bf;
            color:#666
        }
    }
    
}
</style>
